.Parent{
    /* border: 1px solid red; */
    text-align: center;
    background-color: #505659;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
}
.childF{
    padding: 10px;
}
.childF h1{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-style: italic;
    font-size: 25px;
}
.Icons {
    margin-top: 10px;
    font-size: 30px;
    padding: 0px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
}
@media screen and (max-width:500px){
    .child{
        padding: 0px;
    }
    .childF h1{
        display: none;
    }
}